import React, { useState } from 'react';
import { alpha, makeStyles } from '@material-ui/core/styles';
import clsx from 'clsx';
import {
	Fab,
	Grid,
	Toolbar,
	Tooltip,
	IconButton,
	Box,
} from '@material-ui/core';
import AddIcon from '@material-ui/icons/AddTwoTone';
import FilterListIcon from '@material-ui/icons/FilterListTwoTone';
import SearchFilter from '../components/SearchFilter';
import type { Props as SearchFilterProps } from '../components/SearchFilter';

const useToolbarStyles = makeStyles(theme => ({
	root: {
		paddingLeft: theme.spacing(2),
		paddingRight: theme.spacing(2),
	},
	highlight: {
		backgroundColor: alpha(
			theme.palette.primary.main,
			theme.palette.action.activatedOpacity
		),
	},
	title: {
		flex: '1 1 100%',
	},
}));

type Props = {
	onCreate: (e: React.MouseEvent<HTMLButtonElement>) => void;
} & SearchFilterProps;

const PropertyToolbar = (props: Props) => {
	const classes = useToolbarStyles();
	const {
		onCreate,
		onSearch,
		onTriggerSearch,
		statuses,
		search,
		setSearch,
		selectedStatuses,
		setSelectedStatuses,
	} = props;
	const [openFilter, setOpenFilter] = useState(false);

	const toggleFilter = () => setOpenFilter(!openFilter);

	return (
		<>
			<Toolbar className={clsx(classes.root)}>
				<SearchFilter
					onSearch={onSearch}
					onTriggerSearch={onTriggerSearch}
					statuses={statuses}
					search={search}
					setSearch={setSearch}
					selectedStatuses={selectedStatuses}
					setSelectedStatuses={setSelectedStatuses}
					openFilter={openFilter}
				/>
			</Toolbar>
			<Toolbar className={clsx(classes.root)}>
				<Grid container alignItems='center' justifyContent='flex-end'>
					<Box sx={{ display: { xs: 'block', lg: 'none' } }}>
						<Tooltip title='Filter'>
							<IconButton
								sx={{
									width: '48px',
									height: '48px',
								}}
								onClick={toggleFilter}
							>
								<FilterListIcon />
							</IconButton>
						</Tooltip>
					</Box>
					<Tooltip title='Create'>
						<Fab
							color='primary'
							aria-label='add'
							sx={{
								boxShadow: 'none',
								ml: 1,
								width: '48px',
								height: '48px',
							}}
							onClick={onCreate}
						>
							<AddIcon />
						</Fab>
					</Tooltip>
				</Grid>
			</Toolbar>
		</>
	);
};

export default PropertyToolbar;
