import React, { useState } from 'react';
import { Tab, Tabs } from '@material-ui/core';
import { useParams } from 'react-router-dom';
import { makeStyles } from '@material-ui/styles';
import _get from 'lodash.get';
import { gql, useQuery } from '@apollo/client';
import PropertyEdit from '../../application/property/PropertyEdit';
import { PropertyDetail } from '../../application/property/PropertyDetail';
import MainCard from '../../../ui-component/cards/MainCard';
import { TabPanel } from '../../application/components/TabPanel';
import Calendar from '../../application/property/components/Calendar';
import OfferList from '../../application/property/components/OfferList';
import Advanced from '../../application/property/components/Advanced';
import graphqlSchema from '../../../graphql';
import config from '../../../config';
import Loader from '../../../ui-component/Loader';

const useStyles = makeStyles({
	toolbar: {
		'&&': {
			padding: 0,
		},
	},
	tab: {
		textTransform: 'none',
	},
});

export const TABS = {
	edit: 'edit',
	details: 'details',
	calendar: 'calendar',
	offers: 'offers',
	advanced: 'advanced',
};

const EditPage = () => {
	const { id, tab: paramTab } = useParams<{ id: string, tab: string }>();
	const classes = useStyles();
	const [tab, setTab] = useState(paramTab || TABS.details);

	const editPropertyProps = useQuery(gql(graphqlSchema.getProperty), {
		variables: { PropertyId: id },
	});

	const handleChange = (event: React.ChangeEvent<{}>, newValue: string) => {
		setTab(newValue);
	};

	const property = _get(editPropertyProps, 'data.getProperty') || {};
	const propertyNo = _get(property, 'PropertyNo');
	const calendarProfileId = _get(property, 'HomeViewingCalendarProfileId');
	const loading = _get(editPropertyProps, 'loading', false);

	if (loading) return <Loader />;

	const offersListProps = {
		offers: _get(property, 'Offers') || [],
		isLoading: _get(editPropertyProps, 'loading', false),
	};

	return (
		<MainCard title={propertyNo} sx={{ mb: 3 }} contentClass={classes.toolbar}>
			<Tabs value={tab} sx={{ px: 3 }} variant="scrollable" onChange={handleChange}>
				<Tab value={TABS.details} label='Details' classes={{ root: classes.tab }} />
				<Tab value={TABS.edit} label='Edit' classes={{ root: classes.tab }} />
				{calendarProfileId && config.enableCalendar && (
					<Tab
						value={TABS.calendar}
						label='Calendar'
						classes={{ root: classes.tab }}
					/>
				)}
				<Tab
					value={TABS.advanced}
					label='Advanced'
					classes={{ root: classes.tab }}
				/>
				<Tab value={TABS.offers} label='Offers' classes={{ root: classes.tab }} />
			</Tabs>
			<TabPanel value={tab} index={TABS.details}>
				<PropertyDetail property={property} />
			</TabPanel>
			<TabPanel value={tab} index={TABS.edit}>
				<PropertyEdit {...editPropertyProps} />
			</TabPanel>
			<TabPanel value={tab} index={TABS.calendar}>
				{calendarProfileId && <Calendar property={property} />}
			</TabPanel>
			<TabPanel value={tab} index={TABS.advanced}>
				<Advanced property={property} />
			</TabPanel>
			<TabPanel value={tab} index={TABS.offers}>
				<OfferList {...offersListProps} />
			</TabPanel>
		</MainCard>
	);
};

export default EditPage;
